@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Montserrat, sans-serif !important;
}
:root {
  --primary: linear-gradient(45deg, #7002ee, #c174f5);
  --secondary: #4d01a5;
  --primary-soft: #c174f5;
}
body,
html {
  color: #000;
  overflow-x: hidden;
  scroll-behavior: smooth;
  cursor: url("https://res.cloudinary.com/djmafhmnw/image/upload/f_auto,q_auto/v1/Portfolio/32x32"),
    auto;
}
a {
  color: var(--primary);
  text-decoration: none;
}
a:hover {
  color: var(--primary);
  text-decoration: none;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Open Sans", sans-serif;
}
.back-to-top {
  border: 2px solid white;
  position: fixed;
  visibility: hidden;
  opacity: 0;
  right: 30px !important;
  bottom: 30px !important;
  z-index: 9997;
  background: linear-gradient(45deg, var(--primary), var(--primary-soft));
  width: 50px !important;
  height: 50px !important;
  border-radius: 50px;
  transition: all 0.4s;
}
.back-to-top i {
  font-size: 45px !important;
  color: #fff;
  line-height: 0;
  padding: 40px;
}
.back-to-top:hover {
  background: linear-gradient(45deg, var(--primary), var(--primary-soft));
  color: #fff;
}
.back-to-top.active {
  visibility: visible;
  opacity: 1;
}
@media screen and (max-width: 768px) {
  [data-aos-delay] {
    transition-delay: 0 !important;
  }
}
section {
  padding: 60px 0;
  overflow: hidden;
}
.section-title {
  text-align: center;
  padding-bottom: 30px;
}
.section-title h2 {
  font-size: 32px;
  font-weight: 700;
  text-transform: uppercase;
  margin-bottom: 20px;
  padding-bottom: 20px;
  position: relative;
  color: #45505b;
}
.section-title h2::before {
  content: "";
  position: absolute;
  display: block;
  width: 120px;
  height: 1px;
  background: #1a1616;
  bottom: 1px;
  left: calc(50% - 60px);
}
.section-title h2::after {
  content: "";
  position: absolute;
  display: block;
  width: 40px;
  height: 3px;
  background: var(--primary);
  bottom: 0;
  left: calc(50% - 20px);
}
.section-title p {
  margin-bottom: 0;
}
.arrow-image {
  width: 100px;
  margin-top: 40px;
  height: auto;
  transform: rotate(260deg);
  display: block;
}
.blob-image {
  width: 70px;
  padding-bottom: 20px;
}
.floating {
  animation: floating 2.3s ease-in-out infinite;
  transition: 0.7s;
}
@keyframes floating {
  50% {
    transform: translate(0, 20px);
  }
}
.coffee-icon {
  position: fixed;
  bottom: 165px;
  right: 20px;
  background: #f1e20c;
  border-radius: 50%;
  padding: 5px;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.4);
  cursor: pointer;
  z-index: 1000;
  transition: transform 0.3s ease, box-shadow 0.3s ease, opacity 0.3s ease,
    background-color 0.3s ease;
  display: flex;
  justify-content: center;
  align-items: center;
  transform: scale(1);
}
.coffee-icon img {
  width: 50px;
  height: 50px;
  object-fit: contain;
  transition: transform 0.3s ease;
}
.coffee-icon::after {
  content: "Support Me";
  position: absolute;
  bottom: 5px;
  right: 70px;
  background: #ffdd00;
  color: #000;
  padding: 5px 10px;
  border-radius: 5px;
  font-size: 14px;
  opacity: 0;
  transition: opacity 0.3s ease;
  pointer-events: none;
}
.coffee-icon:hover {
  transform: scale(1.1) rotate(15deg);
  box-shadow: 0 10px 18px rgba(0, 0, 0, 0.5);
  opacity: 0.9;
  background: #ffdd00;
}
.coffee-icon:hover::after {
  opacity: 1;
}
.coffee-icon:hover img {
  transform: scale(1.2);
}
