.cell-phone {
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.4);
  background-color: #0a50e9;
  color: #fff !important;
  border-radius: 10px;
  transition: 0.7s;
  padding: 3px;
  font-size: 40px;
}
