:root {
  --primary: #7002ee;
  --secondary: #4d01a5;
  --primary-soft: #c174f5;
  --ripple-color: rgba(112, 2, 238, 0.3);
  --wave-duration: 2s;
}
#preloader {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 9999;
  display: flex;
  justify-content: center;
  align-items: center;
  backdrop-filter: blur(10px);
  overflow: hidden;
  background: #fff;
}
.loader {
  width: 80px;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  border: 8px solid var(--primary);
  border-radius: 50%;
  animation: spin 1.5s ease-in-out infinite;
  overflow: hidden;
  position: relative;
}
.inner-loader {
  position: absolute;
  width: 60px;
  height: 60px;
  border: 6px solid var(--primary);
  border-top-color: transparent;
  border-bottom-color: transparent;
  border-radius: 50%;
  animation: spin 1.5s ease-in-out infinite;
}
.preloader-logo {
  position: absolute;
  width: 40px;
  height: 40px;
  object-fit: contain;
  z-index: 10;
}
.loader:before {
  content: "";
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200px;
  height: 200px;
  border-radius: 50%;
  background: rgba(112, 2, 238, 0.1);
  box-shadow: 0 0 25px rgba(112, 2, 238, 0.3);
  animation: wave 2s ease-in-out infinite;
  pointer-events: none;
  z-index: 1;
}
@keyframes wave {
  0% {
    transform: scale(1);
    opacity: 0.6;
  }
  25% {
    transform: scale(1.5);
    opacity: 0.4;
  }
  50% {
    transform: scale(2);
    opacity: 0.2;
  }
  75% {
    transform: scale(1.5);
    opacity: 0.4;
  }
  100% {
    transform: scale(1);
    opacity: 0.6;
  }
}
@keyframes spin {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}
