:root {
  --primary: #7002ee;
  --secondary: #4d01a5;
  --primary-soft: #c174f5;
}
#footer {
  font-size: 14px;
  text-align: center;
  padding: 0;
  overflow: hidden;
  position: relative;
  margin-top: 0;
  color: #fff;
  background: linear-gradient(135deg, rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.2));
}
#footer h3 {
  font-size: 36px;
  font-weight: 700;
  font-family: Poppins, sans-serif;
  margin-bottom: 15px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);
}
#footer .copyright {
  margin-bottom: 5px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);
}
#footer .credits {
  font-size: 13px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);
}
#footer-video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: 1;
}
#footer .container {
  position: relative;
  z-index: 1;
}
#footer {
  background: 0 0 !important;
  transition: all 0.3s ease;
  margin: 0;
}
footer div img {
  border: 5px solid #fff;
  border-radius: 50%;
}
.floating {
  animation: float 3s ease-in-out infinite;
}
@keyframes float {
  0%,
  100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px);
  }
}
@media screen and (max-width: 768px) {
  #footer h3 {
    font-size: 28px;
  }
  #footer .social-links {
    flex-direction: column;
  }
  #footer .social-links a {
    font-size: 24px;
  }
}
