:root {
  --primary: #7002ee;
  --secondary: #4d01a5;
  --primary-soft: #c174f5;
}
#hero {
  width: 100%;
  height: 100vh;
  background-size: cover;
  position: relative;
}
@media (min-width: 992px) {
  #hero {
    padding-left: 160px;
  }
}
#hero:before {
  content: "";
  position: absolute;
  bottom: 0;
  top: 0;
  left: 0;
  right: 0;
}
#hero h1 {
  margin: 0;
  font-size: 64px;
  font-weight: 700;
  line-height: 56px;
  color: #fff;
}
.hero-text {
  text-shadow: 4px 4px 8px rgba(0, 0, 0, 0.6);
  background-color: #00000034;
  padding: 20px 0;
  border-radius: 15px;
}
#hero p {
  color: #fff;
  margin: 15px 0 0 0;
  font-size: 26px;
  font-family: Poppins, sans-serif;
}
#hero p .typed {
  color: #ffffff;
  font-size: larger;
  border-radius: 10px;
  line-height: 40px;
  padding: 5px 10px;
  background-color: #00000042;
  text-shadow: 10px 10px 10px rgba(0, 0, 0, 0.6);
  letter-spacing: 1px;
}
#hero .social-links a {
  font-size: 24px;
  display: inline-block;
  color: #45505b;
  line-height: 1;
  margin: 15px;
  transition: 0.3s;
}
@media (max-width: 992px) {
  #hero {
    text-align: center;
  }
  #hero h1 {
    font-size: 32px;
    line-height: 36px;
  }
  #hero p {
    margin-top: 10px;
    font-size: 20px;
    line-height: 24px;
  }
}
#hero {
  position: relative;
  width: 100%;
  height: 100vh;
  overflow: hidden;
}
#hero-video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
}
.github,
.linkedin {
  font-size: 35px !important;
  transition: 0.7s !important;
}
.discord {
  font-size: 30px !important;
  transition: 0.7s !important;
}
.linkedin {
  background-color: #1e55a7;
  color: #fff !important;
  border-radius: 4px;
  transition: 0.7s;
  padding: 3px;
}
.github {
  background-color: #000;
  color: #fff !important;
  border-radius: 50px;
  transition: 0.7s;
  padding: 3px;
}
.discord {
  background-color: #6644e0;
  color: #fff !important;
  border-radius: 15px;
  transition: 0.7s;
  padding: 7px;
}
.hero-button {
  display: inline-block;
  padding: 10px 20px;
  font-size: 13px;
  font-weight: 600;
  color: #fff;
  text-decoration: none;
  border: 1px solid rgba(255, 255, 255, 0.712);
  border-radius: 15px;
  background-color: rgba(0, 0, 0, 0.158);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  transition: all 0.3s ease-in-out;
}
.hero-icon-button {
  font-size: 25px;
  padding-right: 10px;
  font-weight: 600;
  color: #fff;
}
.hero-button:hover {
  transform: scale(1.05);
}
.glass-popup {
  background: rgba(187, 93, 250, 0.116);
  backdrop-filter: blur(10px);
  border-radius: 15px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);
  color: #fff;
}
.glass-popup .swal2-title {
  color: #fff;
}
.glass-popup .swal2-html-container {
  color: #ddd;
}
.glass-popup .swal2-cancel,
.glass-popup .swal2-confirm,
.glass-popup .swal2-deny {
  background-color: rgba(0, 0, 0, 0.3);
  color: #fff;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}
.glass-popup .swal2-cancel:hover,
.glass-popup .swal2-confirm:hover,
.glass-popup .swal2-deny:hover {
  background-color: rgba(255, 255, 255, 0.5);
}

